export const getVideoBlob = async (url) => {
	try {
		// 
		const response = await fetch(url);				
		// HTTP
		if (!response.ok) {
			throw new Error(`: ${response.status} ${response.statusText}`);
		}
		// 2.  Blob 
		const blob = await response.blob();
		// Blob URL
		const blobUrl = URL.createObjectURL(blob);
		return blobUrl;
	} catch (error) {
		console.error(":", error);
		throw error;
	}
};
