import Vue from 'vue';
import Router from 'vue-router';
import HomePage from './components/Home.vue';
import PlayerPage from './components/Player.vue';
import LoginPage from './components/Login.vue';
import PaymentPage from './components/Payment.vue';
import RegisterPage from './components/Register.vue';
import PlayerHPage from './components/PlayerH.vue';
import NewPlayer from './components/NewPlayer.vue';
import MePage from './components/Me.vue';
import { getToken,setTouristId,getInvitationcode } from "@/utils";
import { recordUserBehavior } from "@/api/h5api";
Vue.use(Router);


// metaaction
const router = new Router({
	routes: [
		{ path: '/', component: HomePage ,meta:{action:""}},
		{ path: '/player', name: 'Player', component: PlayerPage, meta: { requiresAuth: true,action:"" } },
		{ path: '/playerh', name: 'PlayerH', component: PlayerHPage,meta:{action:""} },
		{ path: '/newPlayer', name: 'newPlayer', component: NewPlayer,meta:{action:""} },
		{ path: '/me', name: 'Me', component: MePage, meta: { requiresAuth: true ,action:""}  },
		{ path: '/login', component: LoginPage ,meta:{action:""} },
		{ path: '/register', component: RegisterPage ,meta:{action:""}  },
		{ path: '/payment', component: PaymentPage, meta: { requiresAuth: true,action:""} }
	]
});

// 
router.beforeEach((to, from, next) => {
	const isAuthenticated = getToken();
    const invitationcode = getInvitationcode();
	// id
	setTouristId();
	recordUserBehavior(to.fullPath,invitationcode,to.meta.action);
	if (to.matched.some(record => record.meta.requiresAuth)) {
		// 
		if (!isAuthenticated ) {
			next({
				path: '/login',
				query: { redirect: to.fullPath }  // 
			});
		} else {
			next();
		}
	} else {
		// 
		next();
	}
});

export default router; 