import axios from "axios";
import {getUserInfo,getTouristId } from "@/utils";
const albURL = process.env.VUE_APP_ALB_API 
// const albApi = axios.create({ albURL });

export const getRecommendedImg = async () => {
	try {
		const response = await axios.get(albURL+"/api/getRecommendedImg");
		return response.data;
	} catch (error) {
		console.error(":", error);
        return [];
		// throw error;
	}
};

//
export const syncUser = async (userId) => {
	try {
		const response = await axios.post(albURL+`/api/thirdpartyUser/syncUserInfo`, {
			userId: userId
		});
		return response.data;
	} catch (error) {
		console.error("token:", error);
		throw error;
	}
};

//token
export const syncToken = async (token,tokenexp,userId) => {
	try {
		const response = await axios.post(albURL+`/api/thirdpartyUser/syncUserToken`, {
			userToken: token,
			expireTime: tokenexp,
			userId: userId
		});
		return response.data;
	} catch (error) {
		console.error("token:", error);
		throw error;
	}
};

//
export const syncOrder = async (oId,userId,amount,currency,paymentMethod,status,payType,payChannel) => {
	try {
		const response = await axios.post(albURL+`/api/order/createOrder`, {
			orderId: oId,
			userId: userId,
			amount: amount,
			currency: currency,
			paymentMethod: paymentMethod, 
			status: status, 
			payType: payType, 
			payChannel: payChannel 
		});
		return response.data;
	} catch (error) {
		console.error(":", error);
		throw error;
	}
};

//
export const getPaySign = async (oId,amount,currency,pm_id) => {
	try {
		const response = await axios.post(albURL+`/api/order/createTransactionSign`, {
			order_id: oId,
			amount: amount,
			currency: currency, 
			pm_id: pm_id, 
		});
		return response.data;
	} catch (error) {
		console.error(":", error);
		throw error;
	}
};

// 
export const recordUserBehavior = async (click_url,code,remark) => {
	if(getUserInfo() || getTouristId()){
		const user_id = getUserInfo()?.userId || getTouristId();
		try {
			// 
			await axios.post(albURL+'/api/userBehavior/addUserBehavior', {
				// click_id: ,
				userId: user_id,
				clickUrl: click_url,
				code: code?code:'',
				remark:remark
			});
		} catch (error) {
			console.error(':', error);
		}
	}
};

// 
export const setUserPayStrategy= async (state) => {

	if(getUserInfo() || getTouristId()){
		const user_id = getUserInfo()?.userId || getTouristId();

		try {
			// 
			await axios.post(albURL+'/api/thirdpartyUser/updateAutoPayStatus', {
				// click_id: ,
				userId: user_id,
				autoPay: state
			});
		} catch (error) {
			console.error(':', error);
		}
	}
};

